export default ()=> {
      return (
        <div className="question" data-index="137" dangerouslySetInnerHTML = {{ __html: `
<h6>137. 以下哪一项会对对象 <code>person</code> 有副作用？</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> person <span class="token operator">=</span> <span class="token punctuation">{</span>
	name<span class="token operator">:</span> <span class="token string">"Lydia Hallie"</span><span class="token punctuation">,</span>
	address<span class="token operator">:</span> <span class="token punctuation">{</span>
		street<span class="token operator">:</span> <span class="token string">"100 Main St"</span>
	<span class="token punctuation">}</span>
<span class="token punctuation">}</span><span class="token punctuation">;</span>

Object<span class="token punctuation">.</span><span class="token function">freeze</span><span class="token punctuation">(</span>person<span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre>
<ul>
<li>A: <code>person.name = &quot;Evan Bacon&quot;</code></li>
<li>B: <code>delete person.address</code></li>
<li>C: <code>person.address.street = &quot;101 Main St&quot;</code></li>
<li>D: <code>person.pet = { name: &quot;Mara&quot; }</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: C</h4>
<p>使用方法 <code>Object.freeze</code> 对一个对象进行 <em>冻结</em>。不能对属性进行添加，修改，删除。</p>
<p>然而，它仅 对对象进行 <em>浅</em> 冻结，意味着只有 对象中的 <em>直接</em> 属性被冻结。如果属性是另一个 object，像案例中的 <code>address</code>，<code>address</code> 中的属性没有被冻结，仍然可以被修改。</p>
</p>
</details>
` }}></div>
      );
    }